<template>
    <div>
        <headers :title="title"></headers>
        <div class="reviseDiv">
            <ul>
                <li>
                   <input placeholder="旧卡密" type="password">
                </li>
                 <li>
                    <input  placeholder="新卡密  （6位及其以上）" type="password">
                </li>
                 <li>
                    <input  placeholder="确认卡密" type="password">
                </li>
            </ul>
        </div>
    </div>
</template>

<style lang="less" scoped>
    .reviseDiv{
        font-family: "PingFang-SC-Medium";
        margin-top: 180px;
        width: 750px;
        border-top: 1px solid #D4D4D6;
        ul{
            li{
               background: #fff;
               margin-bottom: 23px;
               height: 94px;
               padding-left: 40px;
               display: flex;
               input{
                   width: 400px;
                   font-size: 29px;
                   line-height: 94px;
                   color: #000;
               }
            }
        }

    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/revise.vue'
export default {
  components:{
      headers
  },
  data(){
      return{
          title: "修改卡密"
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(247,246,247)";
  }
}
</script>



